module.exports = "import { Component, ViewChild } from '@angular/core';\n\n@Component({\n  templateUrl: './result-list-basic.component.html',\n  selector: `sds-result-list-basic-demo`,\n})\nexport class ResultListBasic {\n  @ViewChild('templateModel') messagesTemplate;\n  templateMessageModel: any = {};\n\n  constructor() {}\n  updateTemplate() {\n    this.templateMessageModel = {\n      results: [],\n      metadata: {\n        messages: this.messagesTemplate,\n      },\n    };\n    this.items = this.templateMessageModel;\n  }\n  itemsDefault = [\n    { title: 'First', id: 1 },\n    { title: 'Second', id: 2 },\n    { title: 'Third', id: 3 },\n    { title: 'Fourth', id: 4 },\n    { title: 'Fifth', id: 5, hasNewerData: true },\n  ];\n\n  items = {\n    results: this.itemsDefault,\n  };\n\n  errorModel = {\n    results: [],\n    metadata: {\n      messages: [\n        {\n          type: 'error',\n          title: 'Error during the api',\n          message: `Error Search criteria. Please try again\n                      If you continue to have this problem, please contact the <a href='https://www.fsd.gov/'>Federal Service Desk.</a>`,\n          buttons: [\n            {\n              id: 'backward',\n              text: 'Go back',\n              action: this.gobackbutton,\n              ariaLabel: 'Go Back',\n            },\n          ],\n        },\n      ],\n    },\n  };\n\n  infoModel = {\n    results: [],\n    metadata: {\n      messages: [\n        {\n          type: 'info',\n          title: 'No Search',\n          message: `No Search criteria. Please try again\n                      If you continue to have this problem, please contact the <a href='https://www.fsd.gov/'>Federal Service Desk.</a>`,\n          classes: 'usa-custom',\n          buttons: [\n            {\n              id: 'backward',\n              text: 'Go back',\n              classes: 'usa-button--secondary',\n              action: this.gobackbutton,\n            },\n            {\n              id: 'forward',\n              text: 'Go forward',\n              classes: 'usa-button usa-button--secondary width-card margin-y-2',\n            },\n          ],\n        },\n      ],\n    },\n  };\n\n  defaultModel = {\n    results: this.itemsDefault,\n  };\n  emptyModel = {\n    metadata: {\n      messages: [\n        {\n          type: 'empty',\n          title: 'No Search results',\n          message: `We couldn't find a match for your search criteria.`,\n          classes: 'usa-custom',\n          buttons: [\n            {\n              id: 'backward',\n              text: 'Go back',\n              classes: 'usa-button--secondary',\n              action: this.gobackbutton,\n            },\n          ],\n        },\n      ],\n    },\n    results: [],\n  };\n  loadingModel = {\n    metadata: {\n      messages: [\n        {\n          type: 'loading',\n        },\n      ],\n    },\n    results: [],\n  };\n  initialModel = {\n    metadata: {\n      messages: [\n        {\n          type: 'initial',\n          title: 'Search Criteria',\n          message: `Choose your filter to run report`,\n          classes: 'usa-custom',\n        },\n      ],\n    },\n    results: [],\n  };\n  gobackbutton() {\n    console.log('button click');\n  }\n}\n"