module.exports = "import { Component } from '@angular/core';\nimport { FormGroup } from '@angular/forms';\nimport { FormlyFormOptions, FormlyFieldConfig } from '@ngx-formly/core';\n\n@Component({\n  templateUrl: './multicheckbox-expandable.component.html',\n  selector: `sds-formly-multicheckbox-expandable-demo`,\n})\nexport class MultiCheckboxExpandable {\n  form = new FormGroup({});\n  model: any = {};\n  isexpandedOptions = false;\n  options: FormlyFormOptions = {};\n  fields: FormlyFieldConfig[] = [\n    {\n      key: 'entity.socioeconomic',\n      type: 'multicheckbox',\n      templateOptions: {\n        label: 'States',\n        description: 'Select any state',\n        required: true,\n        selectAllOption: true,\n        expandableOptions: true,\n        expandedOptions: this.isexpandedOptions,\n        options: [\n          {\n            key: 'AL',\n            value: 'Alabama',\n            tagText: 'AL',\n          },\n          {\n            key: 'AK',\n            value: 'Alaska',\n            tagText: 'AK',\n          },\n          {\n            key: 'AZ',\n            value: 'Arizona',\n            tagText: 'AZ',\n          },\n          {\n            key: 'AR',\n            value: 'Arkansas',\n            tagText: 'AR',\n          },\n          {\n            key: 'CA',\n            value: 'California',\n            tagText: 'CA',\n          },\n          {\n            key: 'CO',\n            value: 'Colorado',\n            tagText: 'CO',\n          },\n          {\n            key: 'CT',\n            value: 'Connecticut',\n            tagText: 'CT',\n          },\n          {\n            key: 'DE',\n            value: 'Delaware',\n            tagText: 'DE',\n          },\n          {\n            key: 'DC',\n            value: 'District Of Columbia',\n            tagText: 'DC',\n            tagClass: 'sds-tag--info-purple',\n          },\n          {\n            key: 'FL',\n            value: 'Florida',\n            tagText: 'FL',\n          },\n          {\n            key: 'GA',\n            value: 'Georgia',\n            tagText: 'GA',\n          },\n          {\n            key: 'HI',\n            value: 'Hawaii',\n            tagText: 'HI',\n          },\n          {\n            key: 'ID',\n            value: 'Idaho',\n            tagText: 'ID',\n          },\n          {\n            key: 'IL',\n            value: 'Illinois',\n            tagText: 'IL',\n          },\n          {\n            key: 'IN',\n            value: 'Indiana',\n            tagText: 'IN',\n          },\n        ],\n      },\n    },\n  ];\n  onChange(ev) {\n    this.isexpandedOptions = !this.isexpandedOptions;\n    this.fields[0].templateOptions.expandedOptions = this.isexpandedOptions;\n  }\n}\n"