module.exports = "import { Component, OnInit } from '@angular/core';\nimport { FormGroup } from '@angular/forms';\nimport { FormlyFormOptions, FormlyFieldConfig } from '@ngx-formly/core';\nimport { BehaviorSubject } from 'rxjs';\n\n@Component({\n  templateUrl: './filters-optional.component.html',\n  selector: `sds-filters-optional-demo`,\n})\nexport class FiltersOptional implements OnInit {\n  constructor() {}\n\n  results: any = {};\n  form = new FormGroup({});\n  model: any = {};\n  options: FormlyFormOptions = {};\n  public filterChange$ = new BehaviorSubject<object>(null);\n\n  // Groups\n\n  // Accordion Multiple Controls\n  sdsGroupAccordionMultipleControls: FormlyFieldConfig[] = [\n    {\n      key: 'filters',\n      templateOptions: {\n        label: 'Entity Information',\n        group: 'accordion',\n      },\n      fieldGroup: [\n        {\n          key: 'keyword',\n          type: 'input',\n          templateOptions: {\n            type: 'text',\n            label: 'Unique Entity ID',\n            tagText: 'DUNS',\n            tagClass: 'sds-tag--info-purple',\n            placeholder: 'A123456',\n          },\n        },\n        {\n          key: 'multiple.accordion.entity.title',\n          type: 'input',\n          templateOptions: {\n            label: 'Entity Name',\n            placeholder: 'eg: Acme Corporation',\n            description: 'Enter the name of your entity.',\n            required: true,\n          },\n        },\n      ],\n    },\n  ];\n\n  // Accordion Single Control\n  sdsGroupAccordionSingleControl: FormlyFieldConfig[] = [\n    {\n      key: 'single.accordion.entity.title',\n      type: 'input',\n      templateOptions: {\n        label: 'Entity Name',\n        group: 'accordion',\n        placeholder: 'eg: Acme Corporation',\n        description: 'Enter the name of your entity.',\n        required: true,\n      },\n    },\n  ];\n\n  public ngOnInit() {\n    this.filterChange$.subscribe((res) => {\n      this.results = res;\n    });\n  }\n}\n"