module.exports = "import { Component } from '@angular/core';\nimport { FormlyFieldConfig, FormlyFormOptions } from '@ngx-formly/core';\nimport { SdsFormlyDialogData, SdsFormlyDialogComponent } from '@gsa-sam/sam-formly';\nimport {\n  SDSAutocompletelConfiguration,\n  SdsDialogService,\n  SDSSelectedItemModel,\n  SelectionMode,\n} from '@gsa-sam/components';\nimport { AutocompleteSampleDataService } from './autocomplete-sample.service';\n\n@Component({\n  templateUrl: 'formly-modal.component.html',\n  selector: `sds-formly-dialog-demo`,\n  providers: [AutocompleteSampleDataService],\n})\nexport class FormlyDialog {\n  updatedModel: any = {};\n  model: any = {};\n  public settings = new SDSAutocompletelConfiguration();\n  public autocompleteModel = new SDSSelectedItemModel();\n  options: FormlyFormOptions;\n  fields: FormlyFieldConfig[] = [\n    {\n      key: 'entity.name',\n      type: 'input',\n      templateOptions: {\n        label: 'Entity Name',\n        placeholder: 'eg: Acme Corporation',\n        description: 'Enter the name of your entity.',\n        required: true,\n      },\n    },\n    {\n      key: 'entity.samuei',\n      type: 'input',\n      templateOptions: {\n        label: 'Unique Entity ID',\n      },\n    },\n    {\n      key: 'entity.dunuei',\n      type: 'input',\n      templateOptions: {\n        label: 'Unique Entity ID',\n      },\n    },\n    {\n      key: 'entity.cage',\n      type: 'input',\n      templateOptions: {\n        label: 'CAGE / NCAGE',\n      },\n    },\n    {\n      key: 'filters',\n\n      templateOptions: { label: 'Keyword' },\n      fieldGroup: [\n        {\n          key: 'firstName',\n          type: 'autocomplete',\n          templateOptions: {\n            label: 'Auto Complete',\n            hideLabel: true,\n            service: this.service,\n            configuration: this.settings,\n            model: this.autocompleteModel,\n            modelChange: this.changes,\n          },\n        },\n      ],\n    },\n    {\n      key: 'entity.naics',\n      type: 'input',\n      templateOptions: {\n        label: 'NAICS Code',\n      },\n    },\n\n    {\n      key: 'entity.psc',\n      type: 'input',\n      templateOptions: {\n        label: 'Product Service Code',\n        placeholder: 'eg: Acme Corporation',\n        description: 'Enter the name of your entity.',\n        required: true,\n      },\n    },\n  ];\n  constructor(public service: AutocompleteSampleDataService, public dialog: SdsDialogService) {\n    this.setup();\n  }\n  changes(value) {\n    console.log(value);\n  }\n\n  setup() {\n    this.settings.id = 'autocompleteBasic';\n    this.settings.primaryKeyField = 'id';\n    this.settings.primaryTextField = 'name';\n    this.settings.secondaryTextField = 'subtext';\n    this.settings.labelText = 'Autocomplete 1';\n    this.settings.selectionMode = SelectionMode.SINGLE;\n    this.settings.autocompletePlaceHolderText = 'Enter text';\n    this.settings.debounceTime = 350;\n  }\n\n  openDialog() {\n    const data: any = {\n      fields: this.fields,\n      model: this.model,\n      submit: 'Submit',\n      title: 'Formly Dialog',\n      options: this.options,\n      cancel: 'No thanks',\n    };\n\n    const dialogRef: any = this.dialog.open(SdsFormlyDialogComponent, {\n      width: 'medium',\n      data: data,\n    });\n\n    dialogRef.componentInstance.submitFn.subscribe((res) => {\n      this.updatedModel = res;\n      dialogRef.close();\n    });\n\n    dialogRef.componentInstance.cancelFn.subscribe((res) => {\n      this.updatedModel = res;\n      dialogRef.close();\n    });\n  }\n}\n"