module.exports = "import { Component } from '@angular/core';\nimport { FormGroup } from '@angular/forms';\nimport { FormlyFormOptions, FormlyFieldConfig } from '@ngx-formly/core';\n\n@Component({\n  templateUrl: './multicheckbox-group.component.html',\n\n  selector: `sds-formly-multicheckbox-group-demo`,\n})\nexport class MultiCheckboxGroup {\n  form = new FormGroup({});\n  model: any = {};\n  options: FormlyFormOptions = {};\n\n  fields = [\n    {\n      key: 'entity',\n      type: 'multicheckbox',\n      templateOptions: {\n        label: 'Grouped multi-checkbox',\n        groupOptions: {\n          'Contract Opportunities': [\n            {\n              key: 'DE1',\n              value: 'Data entry',\n              tagText: 'Tag',\n            },\n            {\n              key: 'LD',\n              value: 'Listing Data',\n            },\n          ],\n          Entities: [\n            {\n              key: 'POR',\n              value: 'Purpose of Registration',\n            },\n            {\n              key: 'LBN',\n              value: 'Legal Business Name',\n              tooltipText: '<b>Assistance Listing</b><u>Tool tip with style</u>',\n            },\n          ],\n        },\n      },\n    },\n  ];\n}\n"