module.exports = "import { Component } from '@angular/core';\nimport { FormlyFieldConfig, FormlyFormOptions } from '@ngx-formly/core';\n@Component({\n  selector: `horizontal-filter-demo`,\n  templateUrl: `horizontal-filter.component.html`,\n})\nexport class HorizontalFilterDemo {\n  options: FormlyFormOptions = {};\n  model = {};\n  // Default Multiple Controls\n  fields: FormlyFieldConfig[] = [\n    {\n      key: 'status',\n      type: 'multicheckbox',\n      templateOptions: {\n        label: 'Status',\n        group: 'popover',\n        options: [\n          {\n            key: 'active',\n            value: 'Active',\n          },\n          {\n            key: 'inactive',\n            value: 'Inactive',\n          },\n          {\n            key: 'all',\n            value: 'All',\n          },\n        ],\n      },\n    },\n    {\n      key: 'socioeconomic2',\n      type: 'multicheckbox',\n      templateOptions: {\n        label: 'Socio-Economic Status',\n        group: 'popover',\n        options: [\n          {\n            key: 'vet',\n            value: 'Veteran Owned',\n          },\n          {\n            key: 'women',\n            value: 'Women Owned',\n          },\n          {\n            key: 'minority',\n            value: 'Minority Owned',\n          },\n        ],\n      },\n    },\n    {\n      key: 'dateRange',\n      templateOptions: {\n        label: 'Date Range',\n        group: 'popover',\n        closeOnClickOutside: false,\n      },\n      fieldGroup: [\n        {\n          key: 'dateRangeSelect',\n          type: 'select',\n          className: 'display-block',\n          templateOptions: {\n            label: 'Select Date',\n            options: [\n              { label: 'Anytime', value: 'anytime' },\n              { label: 'Past day', value: 'pastDay' },\n              { label: 'Past Week', value: 'pastWeek' },\n              { label: 'Custom Dates', value: 'customDate' },\n            ],\n            hideOptional: true,\n          },\n        },\n        {\n          key: 'createdDate',\n          type: 'datepicker',\n          className: 'display-block',\n          templateOptions: {\n            label: 'Created Date',\n            minDate: new Date(2019, 9, 5),\n            maxDate: new Date(2020, 11, 15),\n            placeholder:\n              'eg: ' +\n              new Date().toLocaleString('en-US', {\n                month: 'short',\n                day: 'numeric',\n                year: 'numeric',\n              }),\n            hideOptional: true,\n          },\n          hideExpression: (model) => {\n            return !model || !model['dateRangeSelect'] || model['dateRangeSelect'] != 'customDate';\n          },\n        },\n        {\n          key: 'expirationDate',\n          type: 'datepicker',\n          className: 'display-block',\n          templateOptions: {\n            label: 'Expires Date',\n            minDate: new Date(2019, 9, 5),\n            maxDate: new Date(2020, 11, 15),\n            placeholder:\n              'eg: ' +\n              new Date().toLocaleString('en-US', {\n                month: 'short',\n                day: 'numeric',\n                year: 'numeric',\n              }),\n            hideOptional: true,\n          },\n          hideExpression: (model) => {\n            return !model || !model['dateRangeSelect'] || model['dateRangeSelect'] != 'customDate';\n          },\n        },\n      ],\n    },\n    {\n      key: 'expirationDateRangeEx',\n      type: 'daterangepickerv2',\n      hide: true,\n      templateOptions: {\n        group: 'popover',\n        label: 'Expiration Date Range',\n        minDate: new Date(2019, 9, 5),\n        maxDate: new Date(2020, 11, 15),\n        placeholder:\n          'eg: ' +\n          new Date().toLocaleString('en-US', {\n            month: 'short',\n            day: 'numeric',\n            year: 'numeric',\n          }),\n        hideOptional: true,\n      },\n    },\n    {\n      key: 'entity',\n      type: 'input',\n      hide: true,\n      templateOptions: {\n        group: 'popover',\n        label: 'Entity Name',\n        placeholder: 'eg: Acme Corporation',\n        description: 'Enter the name of your entity.',\n        required: true,\n      },\n    },\n  ];\n\n  searchModel = {};\n\n  handleSubmit($event) {\n    console.log($event);\n  }\n\n  onSearchModelChange($event) {\n    console.log($event);\n  }\n\n  onFilterChange($event) {\n    console.log($event);\n  }\n}\n"