module.exports = "import { Component, Inject } from '@angular/core';\nimport { FormGroup } from '@angular/forms';\nimport { SdsDialogService, SDS_DIALOG_DATA } from '@gsa-sam/components';\nimport { FormlyFieldConfig, FormlyFormOptions } from '@ngx-formly/core';\n\nexport interface AlertData {\n  title: string;\n  content: string;\n}\n\n/**\n * ALERTS\n * ================================================\n */\n// Error\n@Component({\n  selector: 'sds-dialog-sample-alert',\n  templateUrl: './alert-template.html',\n})\nexport class AlertComponent {\n  constructor(@Inject(SDS_DIALOG_DATA) public data: AlertData) {}\n}\n\n/*\n * MAIN COMPONENT\n * ================================================\n */\n@Component({\n  selector: 'sds-modal-sample',\n  templateUrl: './dialog-alert.component.html',\n})\nexport class DialogAlert {\n  form = new FormGroup({});\n  model: any = {\n    alert: {\n      title: 'Open Link?',\n      message: 'Info Message',\n      type: 'info',\n      size: 'small',\n    },\n  };\n  options: FormlyFormOptions = {};\n  fields: FormlyFieldConfig[] = [\n    {\n      key: 'alert',\n      templateOptions: {\n        label: 'Alert',\n      },\n\n      fieldGroup: [\n        {\n          key: 'title',\n          type: 'input',\n          templateOptions: {\n            label: 'Title',\n            hideOptional: true,\n            placeholder: 'type title here',\n            description: 'Enter the alert modal title.',\n          },\n        },\n        {\n          key: 'message',\n          type: 'input',\n          templateOptions: {\n            label: 'Message',\n            hideOptional: true,\n            placeholder: 'type message',\n            description: 'Enter the alert message.',\n          },\n        },\n        {\n          key: 'type',\n          type: 'select',\n          templateOptions: {\n            label: 'Alert Type',\n            description: 'Select the entity type.',\n            required: true,\n            options: [\n              { label: 'Warning', value: 'warning' },\n              { label: 'Info', value: 'info' },\n              { label: 'Error', value: 'error' },\n              { label: 'Success', value: 'success' },\n            ],\n          },\n        },\n        {\n          key: 'size',\n          type: 'select',\n          templateOptions: {\n            label: 'Alert modal size',\n            description: 'Select the entity type.',\n            required: true,\n            options: [\n              { label: 'Small', value: 'small' },\n              { label: 'Medium', value: 'medium' },\n              { label: 'Large', value: 'large' },\n            ],\n          },\n        },\n      ],\n    },\n  ];\n\n  constructor(public dialog: SdsDialogService) {}\n\n  openAlert(title, content, alert, size) {\n    this.dialog.open(AlertComponent, {\n      alert: alert,\n      width: size,\n      data: { title: title, content: content },\n    });\n  }\n}\n"