module.exports = "import { Component, OnInit } from '@angular/core';\nimport { FormGroup } from '@angular/forms';\nimport { FormlyFormOptions, FormlyFieldConfig } from '@ngx-formly/core';\nimport { map, startWith, tap } from 'rxjs/operators';\nimport { BehaviorSubject } from 'rxjs';\nimport { SDSAutocompletelConfiguration, SDSSelectedItemModel, SelectionMode } from '@gsa-sam/components';\n\ninterface Model {\n  readonly player: string;\n  readonly sport: string;\n  readonly team: string;\n}\n\n@Component({\n  selector: 'sds-formly-conditional',\n  templateUrl: './formly-conditional.component.html',\n})\nexport class FormlyConditionalComponent implements OnInit {\n  results: any;\n  public settings = new SDSAutocompletelConfiguration();\n  public autocompleteModel = new SDSSelectedItemModel();\n  form = new FormGroup({});\n  model: any = {};\n  options: FormlyFormOptions = {};\n  //model: Partial<Model> = { sport: '1' };\n  public filterChange$ = new BehaviorSubject<object>(null);\n  fields: FormlyFieldConfig[] = [\n    {\n      // key: 'conditionalFilters',\n\n      wrappers: ['filterwrapper'],\n      templateOptions: { label: 'Conditional Filters' },\n      fieldGroup: [\n        {\n          template:\n            \"<h4 class='margin-top-5 margin-bottom-1'>Hide Input Field :</h4><p class='margin-top-0'>First Input value will reveal the second Input.</p>\",\n        },\n        {\n          key: 'firstInput',\n          type: 'input',\n          templateOptions: {\n            label: 'First Input',\n            options: [\n              {\n                label: 'First Input',\n                placeholder: 'Type here to reveal the second input',\n              },\n            ],\n          },\n        },\n        {\n          key: 'secondInput',\n          type: 'input',\n          hideExpression: (model) => !this.model.firstInput,\n          templateOptions: {\n            label: 'Second Input',\n            options: [\n              {\n                label: 'Second Input',\n              },\n            ],\n          },\n        },\n      ],\n    },\n    {\n      wrappers: ['filterwrapper'],\n      fieldGroup: [\n        {\n          template:\n            \"<h4 class='margin-top-5 margin-bottom-1'>Dropdpwn Radio Selection :</h4><p class='margin-top-0'>Dropdown selection will reveal the respective Radio action.</p>\",\n        },\n        {\n          key: 'check',\n          type: 'select',\n          defaultValue: 'select',\n          templateOptions: {\n            hideOptional: true,\n            label: 'Dropdown Radio',\n            options: [\n              { label: '- Select -', value: 'select' },\n              { label: 'Red', value: 'red' },\n              { label: 'Green', value: 'green' },\n            ],\n          },\n        },\n        {\n          key: 'red',\n          type: 'radio',\n          hideExpression: (model) => this.model.check !== 'red',\n          templateOptions: {\n            options: [{ label: 'Red', value: 'radio' }],\n          },\n        },\n        {\n          key: 'green',\n          type: 'radio',\n          hideExpression: (model) => this.model.check !== 'green',\n          templateOptions: {\n            options: [{ label: 'Green', value: 'radio' }],\n          },\n        },\n      ],\n    },\n    {\n      wrappers: ['filterwrapper'],\n      fieldGroup: [\n        {\n          template:\n            \"<h4 class='margin-top-5 margin-bottom-1'>Dropdpwn Checkbox Selection :</h4><p class='margin-top-0'>Dropdown selection will reveal the respective checkbox.</p>\",\n        },\n        {\n          key: 'checkbox',\n          type: 'select',\n          defaultValue: 'select',\n          templateOptions: {\n            label: 'Checkbox Dropdown',\n            options: [\n              { label: '- Select -', value: 'select' },\n              { label: 'select checkbox to agree', value: 'agree' },\n              { label: 'select checkbox to disagree', value: 'disagree' },\n            ],\n          },\n        },\n        {\n          key: 'agree',\n          type: 'multicheckbox',\n          hideExpression: (model) => this.model.checkbox !== 'agree',\n          templateOptions: {\n            options: [\n              {\n                label: 'I agree',\n                value: 'agree',\n              },\n            ],\n          },\n        },\n        {\n          key: 'disagree',\n          type: 'multicheckbox',\n          hideExpression: (model) => this.model.checkbox !== 'disagree',\n          templateOptions: {\n            options: [\n              {\n                label: 'I disagree',\n                value: 'disagree',\n              },\n            ],\n          },\n        },\n      ],\n    },\n    {\n      wrappers: ['filterwrapper'],\n      fieldGroup: [\n        {\n          template:\n            \"<h4 class='margin-top-5 margin-bottom-1'>Hide Input :</h4><p class='margin-top-0'>Clicking on checkbox will hide the input field.</p>\",\n        },\n        {\n          key: 'inputField',\n          type: 'input',\n          hideExpression: (model) => this.model.firstField,\n          templateOptions: {\n            placeholder: 'selection of checkbox below will hide this input field.',\n          },\n        },\n        {\n          key: 'firstField',\n          type: 'multicheckbox',\n          templateOptions: {\n            options: [\n              {\n                label: 'Hide Above Input Field',\n              },\n            ],\n          },\n        },\n      ],\n    },\n    {\n      wrappers: ['filterwrapper'],\n      fieldGroup: [\n        {\n          template:\n            \"<h4 class='margin-top-5 margin-bottom-1'>Disabled Input :</h4><p class='margin-top-0'>Type in First Name input will enabled the Last Name input field.</p>\",\n        },\n        {\n          key: 'hideExistingFirstName',\n          type: 'input',\n          templateOptions: {\n            label: 'Frist Name',\n            placeholder: 'Type here to see the other field become enabled...',\n          },\n        },\n        {\n          key: 'text2',\n          type: 'input',\n          templateOptions: {\n            label: 'Last Name',\n            placeholder: 'This one is disabled if there is no text in the other input',\n          },\n          expressionProperties: {\n            'templateOptions.disabled': '!model.hideExistingFirstName',\n          },\n        },\n      ],\n    },\n    {\n      wrappers: ['filterwrapper'],\n      fieldGroup: [\n        {\n          template:\n            \"<h4 class='margin-top-5 margin-bottom-1'>Cascaded :</h4><p class='margin-top-0'>Cascaded Select value chage base on the another dropdown (using observable).</p>\",\n        },\n        {\n          key: 'sport',\n          type: 'select',\n          templateOptions: {\n            label: 'First Select',\n            options: [\n              { id: '1', name: 'Option1' },\n              { id: '2', name: 'Option2' },\n            ],\n            hideLabel: true,\n            configuration: this.settings,\n            model: this.autocompleteModel,\n            valueProp: 'id',\n            labelProp: 'name',\n          },\n        },\n        {\n          key: 'team',\n          type: 'select',\n          templateOptions: {\n            label: 'Second Select',\n            options: [],\n            valueProp: 'id',\n            labelProp: 'name',\n          },\n          hooks: {\n            onInit: (field) => {\n              const teams = [\n                { id: '1', name: 'Option1 (FirstSelect- Option1)', sportId: '1' },\n                { id: '2', name: 'Option1 (FirstSelect- Option2)', sportId: '1' },\n                { id: '3', name: 'Option2 (FirstSelect- Option1)', sportId: '2' },\n                { id: '4', name: 'Option2 (FirstSelect- Option2)', sportId: '2' },\n              ];\n              const sportControl = this.form.get('sport');\n              field.templateOptions.options = sportControl.valueChanges.pipe(\n                startWith(sportControl.value),\n                map((sportId) => teams.filter((team) => team.sportId === sportId)),\n                tap(() => field.formControl.setValue(null))\n              );\n            },\n          },\n        },\n        {\n          key: 'player',\n          type: 'select',\n          templateOptions: {\n            label: 'Third Select',\n            options: [],\n            valueProp: 'id',\n            labelProp: 'name',\n          },\n          hooks: {\n            onInit: (field) => {\n              const players = [\n                { id: '1', name: 'Option1 (Option1 (FirstSelect- Option1))', teamId: '1' },\n                { id: '2', name: 'Option2 (Option1 (FirstSelect- Option1))', teamId: '1' },\n                { id: '3', name: 'Option1 (Option1 (FirstSelect- Option2))', teamId: '2' },\n                { id: '4', name: 'Option2 (Option1 (FirstSelect- Option2))', teamId: '2' },\n                { id: '5', name: 'Option1 (Option2 (FirstSelect- Option1))', teamId: '3' },\n                { id: '6', name: 'Option2 (Option2 (FirstSelect- Option1))', teamId: '3' },\n                { id: '7', name: 'Option1 (Option2 (FirstSelect- Option2))', teamId: '4' },\n                { id: '8', name: 'Option2 (Option2 (FirstSelect- Option2))', teamId: '4' },\n              ];\n              const teamControl = this.form.get('team');\n              field.templateOptions.options = teamControl.valueChanges.pipe(\n                startWith(teamControl.value),\n                map((teamId) => players.filter((player) => player.teamId === teamId)),\n                tap(() => field.formControl.setValue(null))\n              );\n            },\n          },\n        },\n      ],\n    },\n  ];\n\n  submit() {\n    alert(JSON.stringify(this.model));\n  }\n\n  // To display the selected model values\n  public ngOnInit() {\n    this.filterChange$.subscribe((res) => (this.results = res));\n  }\n}\n"